export default function App() {
  let cards = [];
  for(let i=0; i<content.length; i+=3) {
    cards.push(
      <Grid container spacing={3} style={{padding: '10px'}}>
        <ContentGridCard content={content[i]}/>
        <ContentGridCard content={content[i+1]}/>
        <ContentGridCard content={content[i+2]}/>
      </Grid>
    )
  }
  return (
    <React.Fragment>
      <AppBar position='fixed'>
        <Toolbar>
          <IconButton>
            <MenuIcon />
          </IconButton>
          Jordan's food
        </Toolbar>
      </AppBar>
      <div style={{margin: 'auto', paddingTop: '74px', width: '70%'}}>
        <Grid container spacing={3}>
          {cards}
        </Grid>
      </div>
    </React.Fragment>
  );
}

const ContentGridCard = props => {
  const {content} = props;
  if(content){
    return(
      <Grid item xs={4}>
        <Card>
          <CardHeader title={content.name}></CardHeader>
          <CardContent>
            <img style={{maxHeight: '295px', width: '100%', objectFit: 'cover'}} src={content.image} alt=""/>
            <p>
              {content.description}
              <Test/>
            </p>
          </CardContent>
        </Card>
      </Grid>
    )
  }
  return(<div/>)
}

const Test = props => {
  const testDatabase = e => {
    firebase.database().ref('test').push().set({test: 'hello world'})
        .then(function(snapshot) {
            console.log('success');
        }, function(error) {
            console.log('error' + error);
        });
  }

  return (
    <React.Fragment>
      <button onClick={testDatabase}>Try Me</button>
    </React.Fragment>
  )
}